

import Vue from 'vue'
import App from './App'
import router from './router'

import $ from 'jquery'

import NativeDevice from '@/JSNative/device.js'

import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'

import VueClipboard from 'vue-clipboard2'

import lazyload from 'vue-lazyload'

import preview from 'vue-photo-preview'
import 'vue-photo-preview/dist/skin.css'


import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import VueQuickLoadmore from 'vue-quick-loadmore'
Vue.config.productionTip = false

Vue.use(preview, {
tapToToggleControls: false,
clickToCloseNonZoomable: false,
closeEl: false,
arrowEl: false,
fullscreenEl: false,
zoomEl: false,
tapToClose: true
})

require('es6-promise').polyfill()
window.jQuery = $
window.$ = $



if (process.env.NODE__ENV === 'dev' || process.env.NODE__ENV === 'test') {
require('./utils/vconsole')
}

require('flexiblejs')

import('./assets/css/reset.css')

import('./assets/css/common.css')


const FastClick = require('fastclick')
FastClick.attach(document.body)

Vue.use(VueAwesomeSwiper)

Vue.use(AlertPlugin)
Vue.use(ToastPlugin)
Vue.use(ConfirmPlugin)


Vue.use(VueQuickLoadmore)
Vue.use(lazyload, {
preLoad: 2,
effect: 'fadeIn',
threshold: 200,
error: require('./assets/loading-error.png'),

attempt: 2
})

Vue.use(VueClipboard)

Vue.use(NativeDevice)
/* eslint-disable no-new */
new Vue({
el: '#app',
router,
components: { App },
template: '<App/>'
})



